    .nav__logo--mobile {
      display: none; }
  .nav__list {
    margin-right: 100px;
    align-self: center; }
    .nav__list-items {
      display: inline;
      padding: 10px;
      font-family: "lato", sans-serif;
      font-size: 14px; }
      .nav__list-items-link {
        color: black;
        text-decoration: none;
        border-bottom: 1px solid transparent; }
        .nav__list-items-link:hover {
          border-bottom: 1px solid; }
  .nav__hamburger {
    display: none; }

@media (max-width: 768px) {
  .nav {
    flex-direction: row;
    align-items: center;
    padding-left: 15px; }
    .nav__logo {
      display: none; }
      .nav__logo--mobile {
        display: block;
        font-family: "Kaushan Script", cursive;
        font-size: 32px;
        margin: 0px 0px 0px 10px; }
    .nav__list {
      margin: 0px;
      align-self: start; }
      .nav__list-items {
        display: block; }
        .nav__list-items--desktop {
          display: none; }
    .nav__hamburger {
