@import "../../styles/shared";

.nav {
  width: 100%;
  padding: 35px 0px;
  background-color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: fixed;

  &__logo {
    font-family: $heading-2;
    font-size: 32px;
    margin-left: 100px;
    &--mobile {
      display: none;
    }
  }

  &__list {
    margin-right: 100px;
    align-self: center;

    &-items {
      display: inline;
      padding: 10px;
      font-family: $body;
      font-size: 14px;

      &-link {
        color: black;
        text-decoration: none;
        border-bottom: 1px solid transparent;
        &:hover {
          border-bottom: 1px solid;
        }
      }
    }
  }

  &__hamburger {
    display: none;
  }
}

@media (max-width: $tablet) {
  .nav {
    flex-direction: row;
    align-items: center;
    padding-left: 15px;

    &__logo {
      display: none;
      &--mobile {
        display: block;
        font-family: $heading-2;
        font-size: 32px;
        margin: 0px 0px 0px 10px;
      }
    }

    &__list {
      margin: 0px;
      align-self: start;

      &-items {
        display: block;
        &--desktop {
          display: none;
        }
      }
    }

    &__hamburger {
      display: inline-block;
      font-size: 1.4em;
      color: rgba(199, 197, 196, 1);
      margin-right: 50px;
      cursor: pointer;
    }
  }

  .container {
    position: fixed;
    display: flex;
    flex-direction: column;
    background-color: white;
    margin-top: 70px;
    padding-left: 15px;
    width: 100%;
  }
}

